	import {getCarDriverList,
	        deleteCarDriver,
	        loadCarDriverDetail} from '@/api/person/response.js'
	export default {
		data(){
				return{
					query:{
						totalCount: 0,
		                pageSize: 10,
		                currentPage: 1,
						kw:"",
						sort: [
		                            {
		                                direct: "DESC",
		                                field: "ID"
		                            }
		                        ]
					},
					data:[],										
					loadingType: 0,
					contentText: {
						contentdown: "上拉显示更多",
						contentrefresh: "正在加载...",
						contentnomore: "没有更多数据了"
					}
				}
			   },
		onLoad() {
				this.$checkusertoken();
				this.submitsearch();
			},
			//上拉加载
		onReachBottom:function(){
				if(this.loadingType!=0)
				{
					return false;
				}
				this.loadingType=1;
				uni.showNavigationBarLoading();
				this.query.currentPage++;
				getCarDriverList(this.query).then(res=>{
					
					if(res.data.code==200)
					{											
						if(res.data.totalCount==this.data.length)
						{
							this.loadingType=2;
							uni.hideNavigationBarLoading();
							return false;
						}
						this.data = this.data.concat(res.data.data);
					}
					else
					{
						uni.showModal({
							title:'提示',
							content:res.data.message,
							showCancel:false
						})
					}
					this.loadingType=0;
					uni.hideNavigationBarLoading();
				})
			},
	    methods:{
				//搜索
				submitsearch(){
					this.query.currentPage = 1;
					this.loadingType = 0,
					console.log(this.query)
					getCarDriverList(this.query).then(res=>{
						console.log(res)
						if(res.data.code==200)
						{
							console.log(res.data.data)
							this.data = res.data.data;
						}
						else
						{
							uni.showModal({
								title:'提示',
								content:res.data.message,
								showCancel:false
							})
						}
					})
				},
				//新增
				create(){
					uni.navigateTo({
						url:'/pages/person/responsecreate'
					})
				},
				//编辑
				edit(systemUserUuid){
					uni.navigateTo({
						url:'/pages/person/responseedit?uuid='+systemUserUuid
					})
				},
				//删除
				deletemsg(systemUserUuid){
					uni.showModal({
						title: '提示',
						content: '是否确认删除',
						success: (rd) => {
							if (rd.confirm) {
								deleteCarDriver(systemUserUuid).then(res => {
									uni.showModal({
										title: '提示',
										content: res.data.message,
										showCancel: false,
										success: (r) => {
											if (r.confirm) {
												this.submitsearch();
											}
										}
									})
								})
							}
						}
					})
					
				},
				//详情
				detail(systemUserUuid){
					uni.navigateTo({
						url:'/pages/person/responsedetail?uuid='+systemUserUuid
					})
				}
		},
			
    }
